@charset "utf-8";
* {box-sizing:border-box;}
body {margin:0;}
img {border:0;}
a {text-decoration:none;color:black;display:inline-block;padding:15px;}
header {display:flex; flex-wrap:wrap; background-color:#36486b;}
nav {margin:auto; width:100%;}
nav > section{display:inline-block;}
nav > section:first-child {width:30%;}
nav > section:first-child h1 {margin-left:15px;}
nav > section:last-child {width:70%;}